import React, { FC } from "react";
import { Card } from "semantic-ui-react";

export const BusinessCard: FC = (props) => {
    return (
        <div>
            <Card.Content>
                <Card.Header>Company Name</Card.Header>
                <Card.Meta>Company Meta</Card.Meta>
                <Card.Description>
                    Phone number, email
                </Card.Description>
            </Card.Content>
            <Card.Content extra>

            </Card.Content>
        </div>
    );
}