

import React, { useState } from "react";
import { Modal, Button, Form, DropdownItemProps, Icon } from "semantic-ui-react";

import { DateTimeInput } from "semantic-ui-calendar-react";
import { observer } from "mobx-react";
import state, { IOrder, EStatus, EPriority } from "../state/state";

interface IOrderModal {
    open: boolean;
    order?: IOrder;
}

export const useInput = (initialValue: any) => {
    const [value, setValue] = useState(initialValue);

    return {
        value,
        setValue,
        reset: () => setValue(""),
        bind: {
            value,
            onChange: (event: React.ChangeEvent<any>, data: any) => {
                if (data && data.value) {
                    setValue(data.value);
                    return;
                }
                setValue(event.target.value);
            }
        }
    };
};


export const OrderModal: React.FunctionComponent<IOrderModal> = observer((props) => {

    const { value: company, bind: bindCompany, reset: resetCompany } = useInput(props.order?.company);
    const { value: tag, bind: bindTag, reset: resetTag } = useInput(props.order?.tag);
    const { value: pounds, bind: bindPounds, reset: resetPounds } = useInput(props.order?.pounds);
    const { value: priority, bind: bindPriority, reset: resetPriority } = useInput(props.order?.priority);
    const { value: hook, bind: bindHook, reset: resetHook } = useInput(props.order?.hook);
    const { value: notes, bind: bindNotes, reset: resetNotes } = useInput(props.order?.description);


    const companyOptions: DropdownItemProps[] = Object.values(state.companies).map(company => {
        return {
            key: company.name,
            text: company.name,
            value: company.name
        }
    });

    const handleClose = () => {
        resetPounds();
        state.orderModelOpen = false;
    }

    const handleSubmit = () => {
        state.orders.push({
            id: state.id++,
            tag,
            company,
            pounds,
            priority,
            hook,
            status: EStatus.Nothing,
        });
        handleClose();
    }

    const priorityOptions: DropdownItemProps[] = [
        {
            text: "None",
            value: EPriority.None
        },
        {
            text: "Get Out",
            value: EPriority.GetOut
        },
        {
            text: "Loading",
            value: EPriority.Loading
        },
        {
            text: "Picksups",
            value: EPriority.Pickups
        },
        {
            text: "Wait On",
            value: EPriority.WaitOn
        },
        {
            text: "Shipping",
            value: EPriority.Shipping
        }
    ]

    return (
        <Modal
            dimmer={"blurring"}
            open={props.open}
            onClose={() => handleClose()}
            closeIcon
        >
            <Modal.Header>New Order</Modal.Header>
            <Modal.Content>
                <Form>
                    <Form.Dropdown required fluid selection label="Company" options={companyOptions} {...bindCompany} />
                    <Form.Input label="Tag" {...bindTag}></Form.Input>
                    <Form.Input required label="Pounds (lb)" {...bindPounds} />
                    <Form.Dropdown required fluid selection label="Priority" defaultValue={5} options={priorityOptions} {...bindPriority} />
                    <Form.Checkbox label="Hook Rack" {...bindHook}></Form.Checkbox>
                    <Form.TextArea label="Notes" />
                </Form>
            </Modal.Content>
            <Modal.Actions>
                <Button positive onClick={() => handleSubmit()}>
                    <Icon name="plus" />
                                Add order
          </Button>
            </Modal.Actions>
        </Modal>
    );
});
