import React, { Component } from "react";

import { StatusControl } from "./components/StatusControl";

import { Button, Container, Table, Icon, Menu, Label, Header, Popup } from "semantic-ui-react"

import { observer } from "mobx-react";

import state, { EPriority, IOrder } from "./state/state";
import { OrderModal } from "./components/OrderModal";
import { BusinessCard } from "./components/BusinessCard";

export function getPriorityString(priority: EPriority) {
    switch (priority) {
        case EPriority.WaitOn:
            return "Wait On"
        case EPriority.Loading:
            return "Loading";
        case EPriority.Pickups:
            return "Pickups";
        case EPriority.Shipping:
            return "Shipping";
        case EPriority.GetOut:
            return "Get Out";
        case EPriority.None:
            return "None";
    }
}

@observer
class App extends Component {

    orderRows(orders: IOrder[]) {
        return orders.map((order, index) => {
            return <Table.Row negative={new Date() > (order.dateEnd as Date)} key={index}>
                <Table.Cell>{order.priority == EPriority.WaitOn ? <Label color="blue" ribbon>{order.id}</Label> : order.id}</Table.Cell>

                <Table.Cell>
                    <Label basic>{order.tag}</Label><Popup
                        on="click"
                        position="right center"
                        trigger={<a style={{ cursor: "pointer" }}>{order.company}</a>}
                    >
                        <BusinessCard />
                    </Popup>
                </Table.Cell>
                <Table.Cell>{getPriorityString(order.priority)}</Table.Cell>
                <Table.Cell>{order.pounds}</Table.Cell>
                <Table.Cell>{order.dateEnd?.toDateString()}</Table.Cell>
                <Table.Cell textAlign="right"><StatusControl order={order} /></Table.Cell>
            </Table.Row>
        });
    }

    completedOrders() {

        if (state.completedOrders.length <= 0) return;

        return (
            <div>
                <Header as="h3">
                    Completed Orders
                </Header>

                <Table compact color="green">
                    <Table.Header>
                        <Table.Row>
                            <Table.HeaderCell>Id</Table.HeaderCell>
                            <Table.HeaderCell>Company</Table.HeaderCell>
                            <Table.HeaderCell>Priority</Table.HeaderCell>
                            <Table.HeaderCell>Pounds (lb)</Table.HeaderCell>
                            <Table.HeaderCell>Estimated Completion</Table.HeaderCell>
                            <Table.HeaderCell textAlign="right">Status</Table.HeaderCell>
                        </Table.Row>
                    </Table.Header>
                    <Table.Body>
                        {this.orderRows(state.completedOrders)}
                    </Table.Body>
                </Table>
            </div>
        );
    }

    render() {
        return (
            <div style={{ marginTop: "32px" }}>
                <Container>
                    <Menu pointing>
                        <Menu.Item active><Icon name="tasks" />Orders</Menu.Item>
                        <Menu.Item><Icon name="address card" />Companies</Menu.Item>
                        <Menu.Menu position="right">
                            <Menu.Item>Employee 012</Menu.Item>
                            <Menu.Item><Icon name="log out" />Logout</Menu.Item>
                        </Menu.Menu>
                    </Menu>
                    <Button.Group floated="right">
                        <Button primary onClick={() => state.orderModelOpen = true}>
                            <Icon name="plus" />
                                New order
                            </Button>
                    </Button.Group>

                    <Header as="h3">
                        Open Orders
                    </Header>
                    <Table compact>
                        <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell>Id</Table.HeaderCell>
                                <Table.HeaderCell>Company</Table.HeaderCell>
                                <Table.HeaderCell>Priority</Table.HeaderCell>
                                <Table.HeaderCell>Pounds (lb)</Table.HeaderCell>
                                <Table.HeaderCell>Estimated Completion</Table.HeaderCell>
                                <Table.HeaderCell textAlign="right">Status</Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body>
                            {this.orderRows(state.openOrders)}
                        </Table.Body>
                    </Table>
                    {this.completedOrders()}
                </Container>
                <OrderModal open={state.orderModelOpen} />
            </div>
        );
    }
}

export default App;
