import { observable, computed } from "mobx";

export interface ICompany {
    id: number;
    name: string;
    avatar?: string;
    phone?: string;
    email?: string;
}

export enum EStatus {
    Nothing,
    Working,
    Tied,
    Inside,
    Ready,
    Loaded
}

export enum EPriority {
    WaitOn,
    Pickups,
    Shipping,
    Loading,
    GetOut,
    None
}

export interface IOrder {
    id: number;
    company: string;
    status: EStatus;
    tag?: string;
    hook?: boolean;
    pounds: number;
    dateStart?: Date;
    dateEnd?: Date;
    description?: string;
    priority: EPriority;
    complete?: boolean;
}

class State {
    @observable companies: { [key: string]: ICompany } = {};
    @observable orders: IOrder[] = [];
    @observable orderModelOpen: boolean = false;
    id: number = 141;

    @computed get openOrders() {
        return this.orders.filter(order => !(order.status === EStatus.Loaded));
    }

    @computed get completedOrders() {
        return this.orders.filter(order => (order.status === EStatus.Loaded));
    }

    updateStatus(id: number, status: EStatus) {
        const index = this.orders.findIndex(value => value.id === id);

        if (index === -1) return;

        this.orders[index] = { ...this.orders[index], status };
    }

    mock() {
        this.companies = {
            "Microsoft": {
                id: 0,
                name: "Microsoft",
                phone: "123-123-1234"
            },
            "Apple": {
                id: 1,
                name: "Apple",
                phone: "123-123-1234",
                email: "email@apple.com"
            },
            "Google": {
                id: 2,
                name: "Google",
                phone: "123-123-1234",
                email: "email@apple.com"
            }
        }

        this.orders = [
            {
                id: 132,
                company: "Microsoft",
                tag: "22A",
                status: EStatus.Nothing,
                priority: EPriority.GetOut,
                pounds: 1000,
                hook: false,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
            },
            {
                id: 133,
                company: "Google",
                tag: "23A",
                status: EStatus.Ready,
                pounds: 500,
                hook: true,
                priority: EPriority.Loading,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
            },
            {
                id: 134,
                company: "Apple",
                tag: "24A",
                status: EStatus.Nothing,
                priority: EPriority.Pickups,
                pounds: 2000,
                hook: false,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
            },
            {
                id: 135,
                company: "CompanyA",
                tag: "25B",
                status: EStatus.Working,
                priority: EPriority.GetOut,
                pounds: 1000,
                hook: false,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
            },
            {
                id: 137,
                company: "CompanyA",
                tag: "25C",
                status: EStatus.Tied,
                priority: EPriority.WaitOn,
                pounds: 5000,
                hook: true,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
            },
            {
                id: 140,
                company: "CompanyB",
                tag: "26A",
                status: EStatus.Tied,
                pounds: 100,
                hook: false,
                priority: EPriority.GetOut,
                dateEnd: new Date("Mon Oct 12 2020 00:28:14 GMT-0700 (Pacific Daylight Time)"),
                description: "<Some important info>"
            }
        ]
    }

}

export default new State();