import React, { Component } from "react";
import { Button, Icon, SemanticCOLORS, Popup, Label } from "semantic-ui-react";

import state, { EStatus, IOrder } from "../state/state";


const stages = [
    {
        name: "inbox",
        icon: true,
        status: EStatus.Nothing
    },
    {
        name: "1",
        icon: false,
        status: EStatus.Working
    },
    {
        name: "2",
        icon: false,
        status: EStatus.Tied
    },
    {
        name: "3",
        icon: false,
        status: EStatus.Inside
    },
    {
        name: "4",
        icon: false,
        status: EStatus.Ready
    },
    {
        name: "truck",
        icon: true,
        status: EStatus.Loaded
    },
];

function getLabel(index: EStatus) {
    switch (index) {
        case 0:
            return "Nothing";
        case 1:
            return "Working";
        case 2:
            return "Tied";
        case 3:
            return "Inside";
        case 4:
            return "Ready";
        case 5:
            return "Loaded";
        default:
            return "Unknown";
    }
}

function buttons(order: IOrder) {

    function getColor(index: EStatus): SemanticCOLORS | undefined {
        if (index === order.status + 1) return "yellow";
        return index < order.status + 1 ? "green" : undefined;
    }

    return stages.map((stage, index) => {
        return <Popup
            content={getLabel(stage.status)}
            key={index}
            trigger={<Button
                color={getColor(index)}
                onClick={() => state.updateStatus(order.id, stage.status)}
                icon={stage.icon}>
                {stage.icon ? <Icon name={stage.name as any} /> : stage.name}
            </Button>}
        />
    });
}

export const StatusControl: React.FunctionComponent<{ order: IOrder }> = (props) => {
    return (
        <span>
            <span style={{ paddingRight: "16px" }}>{getLabel(props.order.status)}</span>
            <Button.Group style={{ paddingRight: "16px" }}>
                {buttons(props.order)}
            </Button.Group>
            <Button.Group>
                {props.order.hook === true ? <Label color="green">HR</Label> : <Label basic>HR</Label>}

            </Button.Group>
        </span>
    );
}